import React, { useState } from 'react';
import { Redirect } from 'react-router-dom';
import Availablechats from './AvailableChats/AvailableChats';
import ChatWindow from './Chats/ChatWindow';
import ChatHeader from './Header/ChatHeader';
import './home.css'
import MainProfile from './Profile/MainProfile';
import io from 'socket.io-client';

import {SOCKET_SERVER} from './../../settings/server';
import UserContext from '../../ContextAPIs/userContext';

const Home = (props)=>{
    let [userState,setUserState] = useState({})
    console.log(props)
    if(localStorage.getItem("islogged") && localStorage.getItem("islogged")==="true" && 'email' in props.TriedLoggedData){

            

            const socket = io(SOCKET_SERVER);
            console.log(socket)
            /* Sending to Register Current Socket to Email */
            socket.emit("setSocketIDToEmail",props.TriedLoggedData);
            socket.emit("getAllUsers","");

            socket.on("sentToClientMessage",(data)=>{
                console.log(data)
            }) 
            socket.on("allUsersTillDate",(rawJSONData)=>{
                console.log(rawJSONData)
                setUserState(rawJSONData)
            })

            

            return <div className="container">
                <UserContext.Provider value={userState}>
                    <div className="artificial">
                        <div className="titleapp">Clippy Chatter</div>
                        <div className="appContainer">
                            <div className="profile">
                                <MainProfile />
                            </div>
                            <div className="header">
                                <ChatHeader />
                            </div>
                            <div className="availablechats">
                                <Availablechats />
                            </div>
                            <div className="chatwindow">
                                <ChatWindow socket={socket} />
                            </div>
                        </div>
                    </div>
                </UserContext.Provider>
        </div>
    }else{
        return <Redirect to="/" />
    }
}
export default Home